<template lang="html">
  <div class="container">
    <!-- <h1>Uno</h1> -->
    <img class='logo' src="./../assets/uno.png" alt="Uno">

    <div class="setUp" v-show="!start">
      Add players:<br/>
      <input type="text" v-model="newPlayer" v-on:keyup.enter="addPlayer()">
      <button type="button" name="button" @click="addPlayer()">Add</button>
      <div class="" v-for='p in players'>
        {{ p.name }}<br/>
      </div>
      <br>
      <br>
      <div>Point limit: <input type="number" v-model="limit"></div>
      <button type="button" name="button" @click="startGame()">Start game</button>
    </div>

    <div class="gameOn" v-show="start">
      <div class="">
        <button type="button" name="button" @click="switchMode()">Switch mode</button>
        Mode: {{ mode }}
      </div>
      <h2>Score table</h2>
      <br>
      <div class="table-of-points">
        <div class="player-column" v-for="(p, i) in players">
          <div class="outer">
            <div class="inner name">
              {{ p.name }}
            </div>
            <div class="inner" v-for="(score, j) in p.scores">
              <span v-if="(mode == 'downfall' && (!gameOverA || (gameOverA && (gameOverARound >= j)))) || (mode == 'winnerTakesAll' && (!gameOverB || (gameOverB && (gameOverBRound >= j))))"
              :class="{ uno : (score == 'UNO') }">{{ score }}</span>
            </div>
            <div class="inner new-score" v-if="(mode=='downfall' && !gameOverA) || (mode=='winnerTakesAll' && !gameOverB)">
              <!-- <input type="checkbox" v-show="!roundOver" @click="uno(i)"> -->
              <button type="button" name="button" class="red" v-show="!roundOver" @click="uno(i)">Uno</button>
              <input type="text" v-model="p.lastScore" v-show="roundOver && (roundWinner != i)">
              <!-- <span v-show="roundOver && (winner == i)" @click="ununo()">UNO</span> -->
              <button type="button" name="button" class="green" v-show="roundOver && (roundWinner == i)" @click="ununo()">Uno</button>
            </div>
          </div>
          <div class="total" v-show="mode=='downfall'">
            {{ p.totalDownfall }}
          </div>
          <div class="total" v-show="mode=='winnerTakesAll'">
            {{ p.totalTakesAll }}
          </div>
        </div>
      </div>
      <br>
      <div class="center" v-show='roundOver'>
        <button type="button" name="button" @click="submitScores()">Submit</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'uno',
  data() {
    return {
      newPlayer: '',
      players: [],
      limit: 200,
      start: false,
      mode: 'downfall',
      roundOver: false,
      roundWinner: -1,
      gameOverA: false,
      gameOverARound: -1,
      gameOverB: false,
      gameOverBRound: -1.
    }
  },
  methods: {
    addPlayer() {
      if (this.newPlayer !== '') {
        this.players.push({ name: this.newPlayer, scores: [], lastScore: 0, totalDownfall: null, totalTakesAll: 0 });
        this.newPlayer = '';
      }
    },
    startGame() {
      if (this.limit != '') {
        this.start = true;
        this.players.forEach(p => p.totalDownfall = this.limit);
        document.documentElement.style.setProperty("--numPlayers", this.players.length);
      }
    },
    switchMode() {
      if (this.mode == 'downfall') {
        this.mode = 'winnerTakesAll'
      } else {
        this.mode = 'downfall'
      }
    },
    uno(index) {
      this.roundWinner = index;
      this.roundOver = true;
      this.players[index].lastScore = 'UNO';
    },
    ununo() {
      document.querySelectorAll('.new-score input[type=checkbox]').forEach(el => {
        el.checked = false;
      })
      this.roundOver = false;
      this.roundWinner = -1;
    },
    submitScores() {
      let winnerScore = 0;
      this.players.forEach(p => {
        p.scores.push(p.lastScore);
        if (p.lastScore != 'UNO') {
          // first update scores for downfall mode
          if (!this.gameOverA) {
            p.totalDownfall -= p.lastScore;

            if (p.totalDownfall <= 0) {
              this.gameOverA = true;
              this.gameOverARound = p.scores.length-1;
            }
          }
          // calculate score for winner takes all mode
          winnerScore += parseInt(p.lastScore);
        }
      });

      this.players.forEach(p => {
        if (p.lastScore=='UNO') {
          if (!this.gameOverB) {
            p.totalTakesAll += winnerScore;
            if (p.totalTakesAll >= this.limit) {
              this.gameOverB = true;
              this.gameOverBRound = p.scores.length-1;
            }
          }

        }
        p.lastScore = 0;
      });
      this.ununo();
    }
  },
  mounted() {
    window.simulate = () => {
      this.players.forEach(p => {
        let score = Math.floor(Math.random()*50);
        p.scores.push(score);
      });
    }
  }
}
</script>

<style lang="scss" scoped>
:root {
  --numPlayers: 4;
  // --colNum: 5;
  // --gridHeight: 120px;
  // --wrapperH: 40px; // for demonstrating the use of CSS Variables
}
.container {
  text-align: center;

  .logo {
    max-width: 100px;
  }
  .setUp {
    max-width: 500px;
    margin: auto;
    padding: 20px;
  }
  .gameOn {
    text-align: center;
    .table-of-points {
      max-width: 500px;
      display: grid;
      grid-template-columns: repeat(var(--numPlayers), 1fr);
      margin: auto;
      .player-column {
        display: inline-block;
        border: 1px solid lightgray;
        border-top: none;
        border-bottom: none;
        /*width: 30%*/
        .inner {
          &.name {
            text-decoration: underline;
          }
          .uno {
            color: green;
          }
        }
        .new-score {
          height: 50px;
          line-height: 50px;
          input {
            text-align: center;
          }
          .red {
            background-color: red;
          }
          .green {
            background-color: green;
          }
        }
        .total {
          border-top: 1px solid gray;
        }
      }
    }
  }
}

</style>



